// components/DashboardSidebar.tsx
"use client"

import { Home, Users, Activity, FileText, Archive, Bell, Mail, LogOut, ChevronRight } from "lucide-react"
import { Button } from "@/components/ui/button"

interface DashboardSidebarProps {
  activeTab: string
  setActiveTab: (tab: string) => void
  onLogout: () => void
}

export default function DashboardSidebar({ activeTab, setActiveTab, onLogout }: DashboardSidebarProps) {
  const menuItems = [
    {
      section: "ADMIN",
      items: [
        { id: "dashboard", label: "Dashboard", icon: Home },
        { id: "users", label: "Users", icon: Users, hasArrow: true },
        { id: "activities", label: "Activities", icon: Activity },
      ],
    },
    {
      section: "BLOG",
      items: [
        { id: "blogs", label: "Post", icon: FileText, hasArrow: true },
        { id: "categories", label: "Categories", icon: Archive },
      ],
    },
    {
      section: "PERSONAL",
      items: [
        { id: "notifications", label: "Notifications", icon: Bell, badge: "5" },
        { id: "links", label: "Links", icon: Mail, badge: "2 New" },
      ],
    },
  ]

  return (
    <div className="w-64 bg-[#f8f4f3] border-r border-gray-300 flex flex-col h-screen sticky top-0 overflow-y-auto">
      {/* Logo */}
      <div className="px-6 py-8 border-b border-gray-300">
        <h2 className="font-black text-2xl tracking-tight text-gray-900">
          LOREM <span className="bg-[#f84525] text-white px-2 py-1 rounded-md text-lg">IPSUM</span>
        </h2>
      </div>

      {/* Navigation */}
      <nav className="flex-1 px-4 py-6 space-y-8 overflow-y-auto">
        {menuItems.map((section) => (
          <div key={section.section}>
            <p className="text-xs font-bold text-gray-500 uppercase tracking-wider mb-3 px-2">
              {section.section}
            </p>
            <div className="space-y-1">
              {section.items.map((item) => {
                const Icon = item.icon
                const isActive = activeTab === item.id

                return (
                  <button
                    key={item.id}
                    onClick={() => setActiveTab(item.id)}
                    className={`w-full flex items-center justify-between px-4 py-3 rounded-lg transition-all font-semibold text-sm
                      ${isActive 
                        ? "bg-gray-950 text-white shadow-md" 
                        : "text-gray-700 hover:bg-gray-100 hover:text-gray-900"
                      }`}
                  >
                    <div className="flex items-center gap-3">
                      <Icon size={18} className={isActive ? "text-white" : "text-gray-600"} />
                      <span>{item.label}</span>
                    </div>

                    <div className="flex items-center gap-2">
                      {item.badge && (
                        <span
                          className={`text-xs px-2.5 py-0.5 rounded-full font-medium ${
                            item.badge.includes("New")
                              ? "bg-green-100 text-green-700"
                              : "bg-red-100 text-red-700"
                          }`}
                        >
                          {item.badge}
                        </span>
                      )}
                      {item.hasArrow && (
                        <ChevronRight
                          size={16}
                          className={`${isActive ? "text-white/70" : "text-gray-400"}`}
                        />
                      )}
                    </div>
                  </button>
                )
              })}
            </div>
          </div>
        ))}
      </nav>

      {/* Logout */}
      {/* <div className="p-4 border-t border-gray-300">
        <Button
          onClick={onLogout}
          className="w-full bg-gray-950 hover:bg-black text-white font-semibold rounded-lg py-6 flex items-center justify-center gap-2 transition"
        >
          <LogOut size={18} />
          Logout
        </Button>
      </div> */}
    </div>
  )
}